define('test-client/router', ['exports', 'ember', 'test-client/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route("application");
    this.route("login");
    this.route("client");
  });

  exports['default'] = Router;

});